/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 */
@import "./styles/default";
@import "./styles/mixin";
@import "./styles/variables";
@import "./styles/common";
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@200;300;400;500;600;700;800;900&display=swap");

.wp-block-create-block-ls-footer {
	font-family: var(--font);
	color: red;

	.ud-footer {
		background: var(--heading-color);
		padding-top: 100px;
		position: relative;
		z-index: 1;
		overflow: hidden;

		.shape {
			position: absolute;
			z-index: -1;

			&.shape-1 {
				top: 0;
				left: 0;
			}

			&.shape-2 {
				top: 0;
				right: 0;
			}

			&.shape-3 {
				bottom: 0;
				right: 0;
			}
		}
	}

	.ud-widget {
		margin-bottom: 50px;

		.ud-footer-logo {
			max-width: 160px;
			display: block;
			margin-bottom: 10px;

			img {
				width: 100%;
			}
		}

		.ud-widget-desc {
			font-size: 15px;
			line-height: 24px;
			color: #f3f4fe;
			margin-bottom: 30px;
			max-width: 260px;
		}

		.ud-widget-socials {
			display: flex;
			align-items: center;

			a {
				font-size: 16px;
				color: #dddddd;
				margin-right: 20px;
				text-decoration : none;

				&:hover {
					color: var(--primary-color);
				}
			}
		}

		.ud-widget-title {
			font-weight: 600;
			font-size: 18px;
			margin-bottom: 35px;
			color: var(--white);
		}

		.ud-widget-links {
			list-style: none !important;

			padding-left: 0 !important;

			a {
				display: inline-block;
				margin-bottom: 12px;
				font-size: 15px;
				line-height: 30px;
				color: #f3f4fe;
				text-decoration: none !important;

				&:hover {
					color: var(--primary-color);
					padding-left: 10px;
				}
			}
		}

		.ud-widget-brands {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			a {
				display: block;
				max-width: 120px;
				margin-bottom: 20px;
				margin-right: 20px;

				@media #{$laptop} {
					max-width: 100px;
				}
			}
		}
	}

	.ud-footer-bottom {
		padding: 35px 0;
		border-top: 1px solid rgba(136, 144, 164, 0.43);

		.ud-footer-bottom-left {
			display: flex;
			align-items: center;

			@media #{$xs} {
				justify-content: center;
			}

			a {
				font-weight: normal;
				font-size: 15px;
				color: #f3f4fe;
				margin-right: 30px;
				display: inline-block;

				@media #{$xs} {
					margin: 0px 8px 8px;
				}

				&:hover {
					color: var(--primary-color);
				}
			}
		}

		.ud-footer-bottom-right {
			text-align: right;
			font-weight: normal;
			font-size: 15px;
			color: #f3f4fe;

			a {
				color: var(--primary-color);
				text-decoration: none;

				&:hover {
					text-decoration: underline;
				}
			}

			@media #{$xs} {
				text-align: center;
				margin-top: 10px;
			}
		}
	}
	#back-to-top{
		text-decoration: none;
	}
}
